import React from 'react';
import propTypes from 'prop-types';

class TeamDesc extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
          error: null,
          isLoaded: false,
          items: []
        };
      }
      render(){
          return(
            <div className="w-65">
                <h3 title={this.props.title}>{this.props.name}<span className="header-title">({this.props.title})</span></h3>   
                <p>{this.props.desc}</p>
                <a className="btn" href={this.props.cta}>Visit {this.props.shortName}'s profile</a>
            </div>
          );
      }

}

export default TeamDesc;