import React from 'react';
import {Link} from 'react-router-dom';

class Sidenav extends React.Component{
    render(){
        return(
            <div className="sidenav">
                <Link to='/'><a className="sidenav__item--home"></a></Link>
                <Link to='/Aboutus'><a className="sidenav__item">About</a></Link>
                <Link to='/work'><a className="sidenav__item">Work</a></Link>
                <Link to='/team'><a className="sidenav__item">Team</a></Link>
                <Link to='/contact'><a className="sidenav__item">Contact Us</a></Link>
            </div>
        );
    }
}
export default Sidenav;