import React from 'react';
import { Link } from 'react-router-dom';

class Block extends React.Component{
        constructor(props){
                super(props);
                this.showDesc = this.showDesc.bind(this);
                this.hideDesc = this.hideDesc.bind(this);
                this.state = {isVisible : true};
        }
        showDesc(){
                this.setState({isVisible: false});
        }

        hideDesc(){
                this.setState({isVisible: true});
        }
        render() {
                return(
                       <div className={`box ${this.props.className}`}>
                                <div className={`${this.state.isVisible ? 'visible' : 'hide'}`}>
                                        <div className="block">
                                                <h1 className="content">{this.props.headText}</h1>
                                                <p className="content">{this.props.bodyText}</p>
                                                <a className="front" onClick={this.showDesc}></a>
                                        </div>       
                                </div>
                                <div className={`${this.state.isVisible ? 'hide' : 'visible'}`}>
                                        <div className="desc">
                                                <p className="content">{this.props.desc}</p>
                                               <Link to={this.props.linkTo}> <a className="btn">{this.props.cta}</a></Link>
                                                <span className="close" onClick={this.hideDesc}>X</span>
                                        </div>
                                </div>
                        </div>
                );
        }
}

export default Block;