class Blocks extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
          error: null,
          isLoaded: false,
          items: []
        };
      }
  

    render() {
            return (
                          
                            <div className="blocks">
                              {
                                boxes.map(box => (
                                  <Block 
                                    key={box.key}
                                    headText={box.headText}
                                    // bodyText={box.bodyText}
                                    desc={box.desc}
                                    cta={box.headText}
                                    linkTo = {box.link}
                                  />
                                ))}
                            </div>
                          
                           
            );
    }
}

export default Blocks;
