class Team extends React.Component{
            constructor(props){
                super(props);
                 }     
    render(){
        var settings = {
            dots: true,
            infinite: true,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1,
            autoplay:false,
            autoplaySpeed:3000
          };
        return(
              <div>
                <Sidenav/>
                <div className="container absolute-center">
                <h1 className="text-center">Meet Our Team</h1>
                <Slider {...settings}>
                    {teams.map(team => (
                        <div class="w-90">
                            <div className={`team ${team.class}`}>
                                <Imagelink name={team.name} url={team.url} link1={team.link1} link2={team.link2} link3={team.link3} link4={team.link4}/>
                                <TeamDesc title={team.title} name={team.name} desc={team.desc} cta={team.cta} shortName={team.name.substring(0,4)}/>
                                </div>
                        </div>
                    ))}
                </Slider>  
            </div>
              </div>
        );
    }
}

export default Team;